<template>
    <div class="integration-paint">
        <div class="bg-panel"
             :style="calcBgStyle()">
            <div :style="calcPaintStyle()"
                 style="background-repeat:no-repeat;transform-origin:0 0 0;"
                 class="canvas-panel">
                <component v-if="config.layout"
                           :is="loader[config.layout].default"
                           :state="state"
                           :config="config"></component>
            </div>
        </div>
        <div class="bottom-bar">
            <div class="scale-bar">
                <span class="icon iconfont icon-minus" @click="scale(-1)"></span>
                <el-slider :show-tooltip="paintScale.showTooltip"
                           v-model="paintScale.index"
                           :min="paintScale.min"
                           :max="paintScale.max"
                           :step="paintScale.step">
                </el-slider>
                <span class="icon iconfont icon-plus" @click="scale(1)"></span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "IntePaint",
        props: ["config", "state"],
        data() {
            return {
                // 动态加载组件的存储
                loader: {},
                // 画布缩放
                paintScale: {
                    showTooltip: true,
                    min: 0.25,
                    max: 2,
                    step: 0.125,
                    index: 0.375
                },
            }
        },
        created() {
            this.loadComponent()
        },
        methods: {
            /**
             * 计算背景样式
             */
            calcBgStyle() {
                let scale = this.paintScale.index
                return {
                    width: (this.config.base.size.width * scale + 60) + 'px',
                    height: (this.config.base.size.height * scale + 60) + 'px'
                }
            },
            /**
             * 计算画布样式
             */
            calcPaintStyle() {
                return {
                    width: this.config.base.size.width + 'px',
                    height: this.config.base.size.height + 'px',
                    backgroundColor: this.config.base.backgroundColor,
                    backgroundImage: this.config.base.backgroundImage ?
                        'url(' + this.config.base.backgroundImage + ')' : 'none',
                    backgroundSize: this.calcBgImageSize(),
                    transform: 'scale(' + this.paintScale.index + ')'
                }
            },
            /**
             * 根据配置项调整背景图片尺寸
             */
            calcBgImageSize() {
                switch (this.config.base.scaleMode) {
                    case 1:
                        return 'auto 100%'
                    case 2:
                        return '100% 100%'
                    default:
                        return '100% auto'
                }
            },
            /**
             * 动态加载组件
             */
            loadComponent() {
                if (!this.state.layout || !this.state.layout.length) {
                    return
                }
                for (let i = 0; i < this.state.layout.length; i++) {
                    let comp = this.state.layout[i]
                    if (!comp || !comp.name || this.loader[comp.name]) {
                        continue
                    }
                    this.loader[comp.name] = require("./layout/" + comp.name + ".vue")
                }
            },
            /**
             * 缩放调整
             */
            scale(step) {
                this.paintScale.index += step * this.paintScale.step
            }
        }
    }
</script>

